import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux/es/exports";
import './Information.css';
import { Link } from "react-router-dom";
import { changeDevice ,Devices,changeDeviceT} from "../actions";
import { changeBG } from "../actions";


const Information=(props)=>{
    const deviceName=useSelector(state=>state.Device.name);
    const deviceNumber=useSelector(state=>state.Device.number);
    const devicePassword=useSelector(state=>state.Device.password);
    const deviceOperator=useSelector(state=>state.Device.operator);
    const deviceModel=useSelector(state=>state.Device.model);
    const allData=props.data;
    console.log(allData);


    const [deviceMo,setDeviceMo]=useState(deviceModel);
    const [deviceOp,setDeviceOp]=useState(deviceOperator);
    const [deviceNa,setDeviceNa]=useState(deviceName);
    const [deviceNu,setDeviceNu]=useState(deviceNumber);
    const [devicePa,setDevicePa]=useState(devicePassword);



    const [selectorM,setSelectorM]=useState(false);
    const [selectorO,setSelectorO]=useState(false);


    const dispatch=useDispatch();
    

    const go_to_setting=async()=>{
        // props.changePage('setting')

    }


    const change_select_model=()=>{
        setSelectorM(!selectorM)
    }
    const change_select_operator=()=>{
        setSelectorO(!selectorO);


    }


    const set_name_device=(event)=>{
        setDeviceNa(event.target.value)
        console.log(deviceNa+'\n'+deviceNu+'\n'+devicePa+'\n'+deviceMo+'\n'+deviceOp);
    }
    const set_number_device=(event)=>{
        setDeviceNu(event.target.value)
        console.log(deviceNa+'\n'+deviceNu+'\n'+devicePa+'\n'+deviceMo+'\n'+deviceOp);
    }
    const set_password_device=(event)=>{
        setDevicePa(event.target.value)
        console.log(deviceNa+'\n'+deviceNu+'\n'+devicePa+'\n'+deviceMo+'\n'+deviceOp);
    }
    const set_operator_device=(event)=>{
        setDeviceOp(event.target.innerHTML)
        setSelectorO(false)
        console.log(deviceNa+'\n'+deviceNu+'\n'+devicePa+'\n'+deviceMo+'\n'+deviceOp);

    }
    const set_model_device=(event)=>{
        setDeviceMo(event.target.innerHTML);
        setSelectorM(false)
        console.log(deviceNa+'\n'+deviceNu+'\n'+devicePa+'\n'+deviceMo+'\n'+deviceOp);
        
     }
    const save_new_info=async()=>{
        if(deviceMo==deviceModel){
        dispatch(changeDevice(deviceName,deviceNa,deviceNu,devicePa,deviceMo,deviceOp));
        }else{
            console.log(deviceMo+'******');

          let zonesO=[];
          let remotesO=[];
          let contactO=[];
          let smsO=[];
          let stateO=[];
          let outO=[]  
            for(let i=0 ;i<allData.length;i++){
                if(allData[i].name==deviceName){
                    console.log('*****',allData[i].zones);
                    zonesO=allData[i].zones;
                    remotesO=allData[i].remotes;
                    contactO=allData[i].contact;
                    smsO=allData[i].smss;
                    outO=allData[i].out;
                    stateO=allData[i].states;
                    
                    break;
                    


                }
            }
            console.log('!sms:\n',smsO)
            let zones=[];
            let remotes=[];
            let smss=[];
            let contacts=[];
            let zone;
            let remote;
            let sms;
            let contact;


            switch(deviceMo){
                case 'ECO20':
                    zone=5;
                    remote=10;
                    contact=10;
                    sms=2;

                break;
                case 'AS20':
                    zone=5;
                    remote=10;
                    contact=10;
                    sms=4;
                break;
                case 'AIO21':
                    zone=8;
                    remote=15;
                    contact=10;
                    sms=8;
                break;

            }
            console.log('!!',sms)
            for(let i=0 ;i<zone;i++){
                if(zonesO[i]!=undefined){
                    zones[i]=zonesO[i]

                }else{
                    zones[i]={'name':'خالی','number':i,'state':'غیر فعال'}

                }
                

                console.log('%%%%')
            }
            for(let i=0;i<remote;i++){
                if(remotesO[i]!=undefined){
                    remotes[i]=remotesO[i]

                }else{
                    remotes[i]={'name':'ریموت 1','type':' null','number':i,'state':'غیر فعال'}

                }
                

            }
            for(let i=0;i<contact;i++){
                if(remotesO[i]!=undefined){
                    contacts[i]=contactO[i]

                }else{
                    contacts[i]={number:'خالی',description:'شماره 1',index:1}

                }

            }
            for(let i=0;i<sms;i++){
                if(smsO[i]!=undefined){
                    smss[i]=smsO[i]

                }else{
                    smss[i]={'name':'گروه '+(i+1),'text':'','number':'1'}

                }

            }
            console.log('&&&&',stateO)
            dispatch(changeDeviceT({'name':deviceNa,'password':devicePa,'model':deviceMo,'access':'','number':deviceNu,'operator':deviceOp,
            'zones':zones,
            'remotes':remotes,
            'smss':smss,
            'contact':contacts
            ,'states':{'battery':(deviceMo=='AIO21'? stateO.battery:'not'),'beeper':stateO.beeper,'connection':stateO.connection,'work':stateO.work,'phone':(deviceMo!='ECO20' ? stateO.phone:'not'),'ajir':stateO.ajir}
            ,out:outO
        },deviceNa,deviceName))

        } 
        
    }
    const show_image=(event)=>{
        dispatch(changeBG(event.target.value))
    }
    return(
        <div className="Information">
            <div  className='bg-Page1'>
                <div  className='bgb-Page1'>
                    <div className="header-Setting">
                    <Link to={'/Setting'}><div className="backPart-Setting" onClick={go_to_setting}>
                    

                        </div></Link>
                        <div className="titlPart-Setting" >
                            <p style={{fontWeight:'400',fontSize:'medium'}}>{deviceName}</p>

                        </div>
                        <div className="refresh">
                            <div className=""></div>
                    
                                <div className=''>
                        
                                 {/* <p>بروز رسانی</p> */}

                                </div>
                        </div>

                    </div>



                    <div className="part1-Information">
                        <div className="item-Information">
                            <span className="input-Information"><input type="text" defaultValue={deviceName} onChange={set_name_device}/></span>
                            <span className="text-Information">نام دستگاه</span>
                        </div>

                        <div className="item-Information">
                            <span className="input-Information"><input type="text" defaultValue={deviceNumber}  onChange={set_number_device}/></span>
                            <span className="text-Information">شماره دستگاه </span>
                        </div>

                        <div className="item-Information">
                            <span className="input-Information"><input type="text" defaultValue={devicePassword}  onChange={set_password_device}/></span>
                            <span className="text-Information">رمز دستگاه</span>
                        </div>
                        <div className="item-Information">
                            <span className="input-Information"><input type="text" /></span>
                            <span className="text-Information">رمز نرم افزار</span>
                        </div>
                        <div className="item-Information">
                        <div className="part6Inp-AddDevice" id="part6Inp-Information" style={{zIndex:'3'}}>
                            
                        <span className='selectModel-AddDevice'  id="selectModel-AddDevice">
                            <div onClick={change_select_model} className='partIconAdd_CA'><span className='moreIcon-AddDevice'></span><span className='selected-AddDevice'>{deviceMo}</span></div>
                                <ul style={{height:selectorM ? '':'0px'}}>
                                    <li onClick={set_model_device}>ECO20</li>
                                    <li onClick={set_model_device}>AS20</li>
                                    <li onClick={set_model_device}>AIO21</li>

                            </ul>
                        </span>
                        <span className='titlInp-AddDevice' >مدل دستگاه</span>
                    </div>

                    
                        </div>

                        <div className="item-Information">
                        <div className="part6Inp-AddDevice" id="part6Inp-Information">
                            
                        <span className='selectModel-AddDevice'  id="selectOperator-AddDevice">
                            <div onClick={change_select_operator} className='partIconAdd_CA'><span className='moreIcon-AddDevice'></span><span className='selected-AddDevice'>{deviceOp}</span></div>
                                <ul style={{height:selectorO ? '':'0px'}}>
                                <li onClick={set_operator_device}>همراه اول</li>
                                    <li onClick={set_operator_device}>ایرانسل</li>
                                    <li onClick={set_operator_device}>رایتل</li>
                                    <li onClick={set_operator_device}>سایر</li>

                            </ul>
                        </span>
                        <span className='titlInp-AddDevice' >اپراتور</span>
                    </div>

                    
                        </div>

                        <Link to='/'><div className="part3-Information">
                            <button id="save-Infofrmation" onClick={save_new_info}>ذخیره</button><br />
                            
                        </div></Link>
                        {/* <input type="file" onChange={show_image}/> */}
                    </div>
                 </div>
            </div>
            
            
        </div>
    )

}

export default Information;