import React, { useState } from 'react';
import './Command.css';
import{parseMessage} from '../actions';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';


const Command=(props)=>{
    // alert('COMMAND IS OPEN')
    const [open,setOpen]=useState(false);
    const widthC=(props.isIcon ? '90px':'100vw');
    const heightC=(props.isIcon ? '90px':'100vh')
    const [width,setWidth]=useState(widthC);
    const [height,setHeight]=useState(heightC);
    const [message,setMessage]=useState('');
    const deviceName=useSelector(state=>state.Device.name);
    const deviceModel=useSelector(state=>state.Device.model);
    const smsindex=useSelector(state=>state.Update.index);
    let closeBg=true;
    const name=useSelector(state=>state.Device.name);
    const model=useSelector(state=>state.Device.model)

    const dispatch=useDispatch();
    


    const oepn_command=()=>{
        setWidth('100vw');
        setHeight('100vh');



    }

    const close_command=()=>{
        setWidth(widthC);
        setHeight(heightC);


    }
    const set_message=(event)=>{
        setMessage(event.target.value);
    }

    const parse_message=()=>{
        console.log('333',model)

        dispatch((parseMessage(message,{Name:name,model:model,smsindex})));
        setWidth(widthC);
        setHeight(heightC);
        if(props.can_refresh){
        props.refresh_page()
        }

        
        

    }

    // const close_command_bg=()=>{
    //     setTimeout(()=>{
            
    //         if(closeBg){
    //             close_command();
    //         }
    //     },500)



    // }

    const set_closeBg=()=>{
        // set_closeBg(false);
        // alert('hj')

    }

   
        // oepn_command()
   




    return(
        <div className={props.isIcon ?"Command":'CommandBG'}   style={{width:(width),height:(height),fontSize:'large',fontWeight:'600',backgroundColor:width=='90px' ? '':'#0f0f0f8c'}}>
            {props.isIcon?'':<div className="blure-Command"></div>}
            <div className="text-Command" onTouchStart={set_closeBg} style={{visibility: width=='90px' ?'hidden':'visible' }} >
                <div className="titl-Command">
                    <div><span>( {name} ) : </span><span>پیامک دریافتی دستگاه را وارد کنید </span></div>
                    <p></p>
                </div>
                <textarea type="text" onChange={set_message} maxLength={70} ></textarea>
                <Link to={'/'}><button  onClick={parse_message}>اعمال</button></Link>
                {props.isIcon ? <button onClick={close_command} >انصراف</button>:<Link to='/'><button  >انصراف</button></Link>}
                

            </div>
            { props.isIcon ?(<div className="icon-Command" onClick={oepn_command} >

            </div>):''}
            


        </div>
    )
}

export default Command;