import React from 'react';
import './Device.css';
import { useDispatch, useSelector } from 'react-redux';
import {SetDevice} from '../actions';
import AddDevice from './AddDevice';
import { useState } from 'react';
import 'react-multi-carousel/lib/styles.css';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css';
import { Navigation, Pagination, Scrollbar, A11y } from 'swiper';
import { deleteDevice } from '../actions';
import { Swiper, SwiperSlide } from 'swiper/react';
import Command from './Command';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
import { BrowserRouter, Routes, Route, Link } from "react-router-dom";
// import { Link } from 'react-router-dom';

const Device=(props)=>{
    const model=props.data.model;
    const name=props.data.name;
    const number=props.data.number;
    const password=props.data.password;
    const operator=props.data.operator;
    const states=props.data.states;
    const lowBattery='https://s24.picofile.com/file/8452289734/battery_6_.png';
    const middleBattery='https://s25.picofile.com/file/8452289750/battery_7_.png';
    const fullBattery='https://s24.picofile.com/file/8452289768/battery_8_.png';
    const noBattery='https://s24.picofile.com/file/8452291618/battery_9_.png'
    const bgi=useSelector(state=>state.Device.path);
    const battery=states.battery;
    let bgBattery='url('+(battery<=11 ? lowBattery:( battery<=12 ? middleBattery:(battery<=13 ? fullBattery:noBattery)))+')'
    console.log("444",states);
    // alert(parseFloat(states.battery)<13)
    
    
    
    const [refresh,setRefresh]=useState(false);
    const dispatch=useDispatch();
    
    
    // alert(window.location.hash = '#')
    // const number =useSelector(state=>state.Device.number);

    let active=(model!= 'AlO21 PT' ?('sms:'+number+'?&body=*'+password+'%23'+' full'):'');
    let noActive=(model!= 'AlO21 PT' ?('sms:'+number+'?&body=*'+password+'%23'+'diarm'):'');
    let noconnect=(model!= 'AlO21 PT' ?('sms:'+number+'?&body=*'+password+'%23'+'stop'):'');
    let halfActive=(model!= 'AlO21 PT' ?('sms:'+number+'?&body=*'+password+'%23'+'part'):'');
    let refreshPage=(model!= 'AlO21 PT' ?('sms:'+number+'?&body=*'+password+'%23'+'status'):'');

    console.log(active+'\t'+noActive+'\t'+noconnect+'t'+halfActive+'\t'+refreshPage);

    const refresh_page=()=>{
        setRefresh(!refresh);
    }
   
    const go_to_setting=()=>{
        props.changePage('setting');
        console.log('go to setting page');
        
    }
    const delete_device=()=>{
        dispatch(deleteDevice(name));


    }

    const[addDevice,setAddDevice]=useState(false);
    const add_device=()=>{
        setAddDevice(true);

    }
    const close_add=()=>{
        setAddDevice(false)
    }

      
    let devices=props.data;
    const responsive = {
        desktop: {
          breakpoint: { max: 3000, min: 1024 },
          items: 3,
          slidesToSlide: 3 // optional, default to 1.
        },
        tablet: {
          breakpoint: { max: 1024, min: 464 },
          items: 2,
          slidesToSlide: 2 // optional, default to 1.
        },
        mobile: {
          breakpoint: { max: 464, min: 0 },
          items: 1,
          slidesToSlide: 1 // optional, default to 1.
        }
      };
      const go_to_command=()=>{
        dispatch(SetDevice(name,number,model,password,operator))
      }

    
    return(

    



<SwiperSlide>
    {/* <Command name={name} model={model} refresh_page={refresh_page} can_refresh={true} noScroll={props.noScroll}></Command> */}

        
<div className="Page1  swiper-slide" >
<Link to={'/command'}><div className="icon-Command" onClick={go_to_command} >
    

</div></Link>
{addDevice ? <AddDevice close_add_device={close_add}></AddDevice>:''}


    <div  className='bg-Page1'><div  className='bgb-Page1'  ><div className="Device " > 
        
            <div className="part1-Device" >
                <Link to={'Setting'}><div className="setting-Device"  onClick={go_to_setting}>
                </div></Link>
                <div className="delete-Device" onClick={delete_device}>
                    
                </div>
                <div className="logo-Device">
                   
                    <div className="deviceName-Device">
                        <p>{name}</p>
                    </div>
                    <div className="image-Device" >
                    </div>
                </div>
            </div>
            <div className="part2-Device">
                <div className="controller-Device">
                    <a href={noconnect}><span className='controllerBtns-Device' id='controllerBtn1-Device'><div className="icon-Device" id='icon1-Device'></div><div className="text-Device"><p>قطع اتصال </p></div></span></a>
                    <a href={noActive}><span className='controllerBtns-Device' id='controllerBtn2-Device'><div className="icon-Device" id='icon2-Device'></div><div className="text-Device"><p>غیر فعال</p></div></span></a>
                    <a href={halfActive}><span className='controllerBtns-Device' id='controllerBtn3-Device'><div className="icon-Device" id='icon3-Device'></div><div className="text-Device"><p>نیمه فعال</p></div></span></a>
                    <a href={active}><span className='controllerBtns-Device' id='controllerBtn4-Device'><div className="icon-Device" id='icon4-Device'></div><div className="text-Device"><p>فعال</p></div></span></a>
                    <a href={refreshPage}><span className='controllerBtns-Device' id='controllerBtn4-Device'><div className="icon-Device" id='icon5-Device'></div><div className="text-Device"><p>بروز رسانی </p></div></span></a>
                </div>
            </div>
        </div>



<div className="part2-Page1" >

    {model=='AIO21'?(<div className="battery-Page1">
        <span className='icons-Page1'><div className='icon-Page1' id='icon1-Page' style={{backgroundImage:bgBattery}}></div></span>
        <span className='text-Page1'>
            <div className="title-Page1">
                <p>{(states.battery!='No Batt' ? 'باتری وصل':"باتری قطع")}</p>
            </div>
            <div className="description-Page1">
                <p>{states.battery=='No Batt' ? '0':states.battery}</p>
            </div>
        </span>
    </div>):''}

    <div className="active-Page1">
    <span className='icons-Page1'><div className='icon-Page1' id='icon2-Page'></div></span>
        <span className='text-Page1'>
            <div className="title-Page1">
                <p>{states.work=="Armed Full" ? 'فعال':(states.work=="Armed Part" ? 'نیمه فعال':'غیر فعال')}</p>
            </div>
            <div className="description-Page1">
                <p>وضعیت دستگاه</p>
            </div>
        </span>

    </div>
    {model!='ECO20' ? (<div className="ConnectTel-Page1">
    <span className='icons-Page1'><div className='icon-Page1' id='icon3-Page'></div></span>
        <span className='text-Page1'>
            <div className="title-Page1">
                <p>{states.phone=="OFF" ? "قطع":"وصل"}</p>
            </div>
            <div className="description-Page1">
                <p>وضعیت خط تلفن</p>
            </div>
        </span>

    </div>) :''}
    <div className="alarm-Page1">
    <span className='icons-Page1'><div className='icon-Page1' id='icon4-Page'></div></span>
        <span className='text-Page1'>
            <div className="title-Page1">
                <p>{states.ajir=='No Alarm' ? 'بدون آژیر':(states.ajir=='Alarming' ? 'آژیر زده شده':"غیر فعال")}</p>
            </div>
            <div className="description-Page1">
                <p>وضعیت آژیر</p>
            </div>
        </span>

    </div>
    <div className="notif-Page1">
    <span className='icons-Page1'><div className='icon-Page1' id='icon5-Page'></div></span>
        <span className='text-Page1'>
            <div className="title-Page1">
                <p>{states.beeper=="OFF" ? "قطع": "وصل"}</p>
            </div>
            <div className="description-Page1">
                <p>وضعیت اسپیکر</p>
            </div>
        </span>

    </div>
    <div className="connect-Page1">
    <span className='icons-Page1'><div className='icon-Page1' id='icon6-Page'></div></span>
        <span className='text-Page1'>
            <div className="title-Page1">
                <p>{states.connection=="OFF" ? "قطع":"وصل"}</p>
            </div>
            <div className="description-Page1">
                <p>وضعیت برق دستگاه</p>
            </div>
        </span>
        
    </div>



</div>
<div className="part3-Page1">

</div></div></div>

{/* {states} */}
</div>
</SwiperSlide>

    )
}

export default Device;