import React, { useState } from 'react';
import { useSelector ,useDispatch} from 'react-redux';
import './SmsPart.css';
import { Update } from '../actions';


const SmsPart=(props)=>{
    console.log(props)
    const number=useSelector(state=>state.Device.number);
    const password=useSelector(state=>state.Device.password);
    const [text,setText]=useState('')
    let send='sms:'+number+'?&body=*'+password+'%23text:'+props.index+':'+text;
    let receive='sms:'+number+'?&body=*'+password+'%23text?'+props.index;
    const shortHeight='62px';
    const LongHeight='250px';
    const dispatch=useDispatch()
    
    const [height,setHeight]=useState('62px');
    const [rotate,setRotate]=useState('rotate(90deg)');
    const text_sms=(event)=>{
        setText(event.target.value);

    }
    const open_sms_part=()=>{
        if(height==LongHeight){
            setHeight(shortHeight)
        }else{
            setHeight(LongHeight);
        }
        console.log('close sma editor')
        

    }
    const set_info_update=()=>{
        console.log('send sms index.')
        dispatch(Update(props.index))
    }
    return(
        <div className="SmsPart" style={{height:height}}>
            
            <div id="part1-SmsPart">
                <div className="icon-SmsPart">

                </div>
                <div className="description-SmsPart">
                    <p>{props.name}</p>
                    {/* <p >{props.text}</p> */}
                </div>
                <div className="open-SmartPart" onClick={open_sms_part} style={{transform:rotate}}>

                </div>

            </div>

            <div id="part2-SmsPart">
                <div className="input-SmsPart"  >
                    <textarea type="text" onChange={text_sms} style={{fontSize:'small',fontWeight:'400'}} maxLength={60} defaultValue={props.text}></textarea>
                </div>
                <div className="controllers-SmsPart">
                    <a href={send}><button>ارسال</button></a>
                    <a href={receive} onClick={set_info_update}><button >دریافت</button></a>

                </div>

            </div>
            
            
            

        </div>
    )
}

export default SmsPart;