import React from 'react'
import './Sms.css';
import SmsPart from './SmsPart';
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import {useSelector,useDispatch} from 'react-redux';
import {HideCommand, VisibleCommand} from '../actions/index';
import { Link } from 'react-router-dom';
import Command from './Command';


const Sms=(props)=>{
    // console.log('888888'+props.data[0].text);
    const name=useSelector(state=>state.Device.name)
    const dispatch=useDispatch();
    dispatch(HideCommand())
    const responsive = {
        desktop: {
          breakpoint: { max: 3000, min: 1024 },
          items: 3,
          slidesToSlide: 3 // optional, default to 1.
        },
        tablet: {
          breakpoint: { max: 1024, min: 464 },
          items: 2,
          slidesToSlide: 2 // optional, default to 1.
        },
        mobile: {
          breakpoint: { max: 464, min: 0 },
          items: 1,
          slidesToSlide: 1 // optional, default to 1.
        }
      };
    const go_to_setting=()=>{
        props.changePage('setting')

    }
    return(
        <div className="Sms"><div  className='bg-Setting'><div  className='bgb-Setting'>
            <Command name={name} isIcon={true}></Command>
                
            <div className="header-Setting">
            <Link to={'/Setting'}><div className="backPart-Setting" onClick={go_to_setting}>
                    

                </div></Link>
                <div className="titlPart-Setting" >
                    <p style={{fontWeight:'400',fontSize:'medium'}}>{name}</p>

                </div>
                <div className="refresh">
                    <div className=""></div>
                    
                    <div className=''>
                        {/* <p>بروز رسانی</p> */}

                    </div>
                </div>

            </div>
            

            <div id="part1-Sms">
                {props.data.map((item) => {
                    return(
                        <SmsPart text={item.text} index={item.number} name={item.name}></SmsPart>
                    )
                    
                })}
            </div>



            
            <div className="blure"></div></div></div>
        </div>
    )
}

export default Sms;


