import React from 'react';
import './Setting.css';
import {useSelector,useDispatch} from 'react-redux';
import {HideCommand, VisibleCommand} from '../actions/index';
import { Link } from 'react-router-dom';
import Command from './Command';


const Setting=(props)=>{
    console.log('first command in setting page.');

    // const Range = createSliderWithTooltip(Slider.Range);
    const name=useSelector(state=>state.Device.name)
    const dispatch=useDispatch();
    dispatch(VisibleCommand());
    const go_to_home=()=>{
        props.changePage('home');
        
    }
    const go_to_contact=()=>{
        props.changePage('contact')
    }

    const go_to_sms=()=>{
        props.changePage('sms');
    }

    const go_to_zoon_info=()=>{
        props.changePage('zoonInfo');
        console.log('go to zoon part')
    }

    const go_to_remotes=()=>{
        props.changePage('remotes');
        console.log('go to remotes part');

    }
    const go_to_out_put=()=>{
        props.changePage('outPuts');
        console.log('go to outPut part');
    }

    const go_to_charge=()=>{
        props.changePage('charge');
        console.log('go to charg part');

    }
    const go_to_information=()=>{
        props.changePage('information');
        console.log('go to charg part');

    }
    return(
        <div className="Setting">
            <Command name={name} isIcon={true}></Command>
            <div  className='bg-Setting'><div  className='bgb-Setting'><div className="header-Setting">
            <Link to={'/'}><div className="backPart-Setting" onClick={go_to_home}>
                    

                </div></Link>
                <div className="titlPart-Setting" >
                    <p style={{fontWeight:'400',fontSize:'medium'}}>{name}</p>

                </div>
                <div className="refresh">
                    <div className=""></div>
                    
                    <div className=''>
                        
                        {/* <p>بروز رسانی</p> */}

                    </div>
                </div>

            </div>
            <div className="menu-Setting">
                <div className='part-Setting' id="part1-Setting">
                    <Link to={"/charge"}><div className='item-Setting' id='item1-Setting' onClick={go_to_charge}>
                        <div className="icon-Setting">
                            
                        </div>
                        <div className="text-Setting">
                            <p>شارژ سیم کارت</p>

                        </div>

                    </div></Link>
                    <Link to={"/sms"}><div className='item-Setting' id='item2-Setting' onClick={go_to_sms}>
                        <div className="icon-Setting">
                            
                        </div>
                        <div className="text-Setting">
                            <p>متن پیام ها</p>

                        </div>

                    </div></Link>
                    <Link to={"/contact"}><div className='item-Setting' id='item3-Setting' onClick={go_to_contact}>
                        <div className="icon-Setting">
                            
                        </div>
                        <div className="text-Setting">
                            <p>شماره تلفن ها</p>

                        </div>

                    </div></Link>
                </div>
                <div className='part-Setting' id="part2-Setting" >
                    <Link to={"/outPut"}><div className='item-Setting' id='item1-Setting' onClick={go_to_out_put}>
                        <div className="icon-Setting">

                        </div>
                        <div className="text-Setting">
                            <p>خروجی ها</p>

                        </div>

                    </div></Link>
                    <Link to={'/remote'}><div className='item-Setting' id='item2-Setting' onClick={go_to_remotes}>
                        <div className="icon-Setting">

                        </div>
                        <div className="text-Setting" >
                            <p>ریموت ها</p>

                        </div>

                    </div></Link>
                    <Link to={'/zon'}><div className='item-Setting' id='item3-Setting' onClick={go_to_zoon_info}>
                        <div className="icon-Setting">

                        </div>
                        <div className="text-Setting" >
                            <p>اطلاعات زونها</p>

                        </div>

                    </div></Link>


                </div>
                <div className='part-Setting' id="part3-Setting">
                    <Link to={'/info'}><div className='item-Setting' id='item1-Setting' onClick={go_to_information}>
                        <div className="icon-Setting">

                        </div>
                        <div className="text-Setting" >
                            <p>اطلاعات دستگاه</p>

                        </div>

                    </div></Link>
                    {/* <div className='item-Setting' id='item2-Setting'>
                        <div className="icon-Setting">

                        </div>
                        <div className="text-Setting">
                            <p> رمز ورود</p>

                        </div>

                    </div> */}

                </div>
                


            </div></div></div>

        </div>
    )

};

export default Setting;
