const Update = (state = { name: '', index: '' }, action) => {
    switch (action.type) {
        
        case 'SMS_UPDATE':
            console.log('sms device is:' + action.index);
            console.log('sms index in Update:' + state)
            return state = { name: 'sms', index: action.index }

            break;
        default:
            return state = state;
            break;
    }


}
export default Update;