import React, { useState } from "react";
import './Charge.css';
import { useDispatch, useSelector } from "react-redux/es/exports";
import { HideCommand,VisibleCommand } from "../actions";
import ChargeAlert from "./ChargeAlert";
import { Link } from 'react-router-dom';

const Charge=(props)=>{
    const name=useSelector(state=>state.Device.name)

    const dispatch=useDispatch();
    
    const  [Alert,setAlert]=useState(false);
    const close_alert=()=>{
        setAlert(false)
    }

    

    const go_to_setting=()=>{
        props.changePage('setting')
    }


    return (
        <div className="Charge"  ><div  className='bg-Setting'><div  className='bgb-Setting'>
            {Alert ? <ChargeAlert close_alert={close_alert}></ChargeAlert> :''}
            {/* <div className="blure"></div> */}
            <div className="header-Setting">
                <Link to={'/Setting'}><div className="backPart-Setting" onClick={go_to_setting}>
                   
                    

                </div></Link>
                <div className="titlPart-Setting" >
                    <p style={{fontWeight:'400',fontSize:'medium'}}>{name}</p>

                </div>
                <div className="refresh">
                    {/* <div className="icon-refresh"></div> */}
                    
                    {/* <div className="text-refresh" > */}
                        {/* <p style={{fontWeight:'500',fontSize:'large'}}>بروز رسانی</p> */}

                    {/* </div> */}
                </div>

            </div>
            
            <div id="part1-Charge">

                <p className="tell-Charge">كاربر گرامي لطفا جهت افزايش شارژ رمز شار دريافتي را بدون كد پيشوند يا پسوند, در كادر زير وارد كنيد  </p>
                <div className="increase-Charge">
                    <div className="submitPart-Charge">
                        <button onClick={()=>{setAlert(true)}}>افزايش اعتبار</button>
                       
                    </div>
                    <div className="submitPart-Charge">
                    <button>استعلام</button>
                    

                    </div>
                    

                </div>
            </div>
            </div></div></div>

        
    )
}

export default Charge;