import React, { useState } from "react";
import './ContactAlert.css';
import {useSelector,useDispatch} from 'react-redux';
import {HideCommand, VisibleCommand} from '../actions/index';
import { Link } from "react-router-dom";
const ContactAlert=(props)=>{
    const dispatch=useDispatch();
    dispatch(HideCommand());
    const numberReceiver=useSelector(state=>state.Device.number);
    const password=useSelector(state=>state.Device.password);
    const[selectPort,setSelectPort]=useState(false);
    const [typeCall,setTypeCall]=useState('فقط تماس');

    const set_type_call=(event)=>{
        setTypeCall(event.target.innerHTML);
        setSelectPort(false)

    }

    const [numberSaver,setNumberSaver]=useState('');
    let saveNumber='sms:'+numberReceiver+'?&body=*'+password+'%23num:'+props.index+':'+numberSaver;

    const save_number=()=>{
        
    }

    const change_select_port=()=>{
        setSelectPort(!selectPort);
    }
    return(
       <div className="AddDevice"><div className="ContactAlert">
        
            <div className="alert-ContactAlert">
                <div className="part-ContactAlert" id="part1-ContactAlert">
                    <div className="title-ContactAlert">
                        
                        <p >ذخیره شماره جدید</p>
                    </div>
                    



                </div>
                <div className="part-ContactAlert" id="part2-ContactAlert">

                    <div className="inputPart-ContactAlert">
                        <input type="numberic" onChange={(event)=>{setNumberSaver(event.target.value)}}/>
                    </div>
                    
                    <div className="part6Inp-AddDevice" id="part6Inp-AddDevice">
                        <span className='selectModel-AddDevice'  id="selectModel-AddDevice">
                            <div onClick={change_select_port} className='partIconAdd_CA'><span className='moreIcon-AddDevice'></span><span className='selected-AddDevice'>{typeCall}</span></div>
                                <ul style={{height:selectPort ? '':'0px'}}>
                                    <li onClick={set_type_call}>فقط تماس</li>
                                    <li onClick={set_type_call}>فقط پیامک</li>
                                    <li onClick={set_type_call}>پیامک و تماس</li>
                                    <li onClick={set_type_call}>اظطراری</li>
                                    <li onClick={set_type_call}>شهری</li>
                            </ul>
                        </span>
                        <span className='titlInp-AddDevice' >نوع تماس</span>
                    </div>

                </div>
                <div className="part-ContactAlert" id="part3-ContactAlert">
                    <div className="controlers-ContactAlert" >
                        <a href={saveNumber}><div className="send-ContactAlert" onClick={save_number}>
                            <p>ذخیره</p>


                        </div></a>
                       <div className="cancle-ContactAlert"  onClick={props.close_alert}>
                            <p>انصراف</p> 
                            
                        </div>
                    </div>

                </div>
                </div>

        </div></div>)
}

export default ContactAlert;