const Device = (state={isScroll:false}, action) => {

    if (action.type == 'SET_DEVICE') {
        return (
            state = {
                model: action.model,
                name: action.name,
                number: action.number,
                password: action.password,
                operator: action.operator,
                path:state.path
                
            }
        )

    } else if(action.type=='CHANGE_BG'){
        return state ={
            model: state.model,
            name: state.name,
            number: state.number,
            password: state.password,
            operator: state.operator,
            path:action.path
        }
    }else{
        return state=state
    }



}

export default Device;