export const parseMessage = (message, asset) => {
    console.log('message is parsing:' + message);



    let letter = '';
    let type = '';
    let content = '';
    let end = 0;
    let start = 0;
    let endNumber = 1;
    let data = [];
    for (let i = 1; i <= message.length; i++) {
        letter = message.slice(i - 1, i);
        if (letter == '\n') {
            type = message.slice(0, i);
            content = message.slice(i);
            console.log('typ of message is:\n' + type + '#');
            console.log('content of message is:\n' + content);
            break;

        } else {

        }
    }

    switch (type) {
        case "STATUS\n":
            console.log('parse status message')
            let is3line = false;
            for (let i = 1; i <= content.length + 1; i++) {

                letter = content.slice(i - 1, i);
                if (data.length != 1) {
                    if (letter == ':') {
                        start = i;


                    }
                    if (letter == '\n' || i === (content.length)) {
                        end = i;
                        data.push(content.slice(start, (i === (content.length) ? end : end - 1)));
                        // data.push('1');
                        console.log('beeper in action is' + content.slice(start, end - 1) + 'start' + start + 'end' + end)

                    }
                } else {

                    if (is3line && letter == '\n') {
                        end = i;

                        data.push(content.slice(start, end - 1));

                    }
                    if (!is3line) {
                        start = i - 1;
                        is3line = true;

                    }


                }
            }

            break;

        case 'list:\n':
            let contact = { number: '', access: '' }
            for (let i = 1; i <= (content.length - 1); i++) {
                letter = content.slice(i - 1, i);
                console.log('letter is:' + letter)
                if (letter == ':' && endNumber == 2) {

                    end = i;
                    contact.number = content.slice(start, end - 1);
                    console.log('##end is:' + end + '\nnumber is:' + contact.number);
                    start = i;
                    endNumber = 3



                }
                if (letter == ':' && endNumber == 1) {
                    start = i;
                    endNumber = 2;
                    console.log('##start is:' + start)

                }

                if (letter == ':' && endNumber == 3) {
                    start = i;
                    endNumber = 4;

                    // end = i;
                    // contact.access = content.slice(start, (i == content.length ? end : (end - 1)));
                    // endNumber = 1;
                    // console.log('##access is' + contact.access + '\t' + 'number is' + contact.number);
                    // data.push(contact);

                    // // console.log('##data is:\n' + contact.access + '\t' + contact.number);
                    // contact.number = '';
                    // contact.access = '';

                }
                if ((letter = '\n' || i == (content.length)) && endNumber == 4) {
                    end = i;
                    contact.access = content.slice(start, end + 1);
                    console.log('#data is:' + contact.access + '\nend:' + end + '\n' + 'start' + start);
                    data.push(contact);
                    contact = { access: "", number: "" }
                    endNumber = 1;

                }
            }
            endNumber = 1;
            // console.log(data)


            break;
        case 'remotes:\n':

            let remote = { name: '', active: '' }
            for (let i = 1; i <= (content.length - 1); i++) {
                console.log('content length' + content.length + '\nendNumber:' + endNumber);

                letter = content.slice(i - 1, i);
                console.log('letter is:' + letter)
                if (letter == ':' && endNumber == 2) {
                    end = i;
                    remote.name = content.slice(start, end - 1);
                    console.log('@@end is:' + end + '\nitem1' + remote.name)
                        // console.log('##end is:' + end + '\nnumber is:' + contact.number);
                    start = i;
                    endNumber = 3



                }
                if (letter == ':' && endNumber == 1) {
                    start = i;
                    endNumber = 2;
                    console.log('##start is:' + start)

                }

                if (letter == ':' && endNumber == 3) {
                    start = i;
                    endNumber = 4;


                    // end = i;
                    // contact.access = content.slice(start, (i == content.length ? end : (end - 1)));
                    // endNumber = 1;
                    // console.log('##access is' + contact.access + '\t' + 'number is' + contact.number);
                    // data.push(contact);

                    // // console.log('##data is:\n' + contact.access + '\t' + contact.number);
                    // contact.number = '';
                    // contact.access = '';

                }
                if ((letter = '\n' || i == (content.length - 1)) && endNumber == 4) {
                    end = i;

                    remote.active = content.slice(start, end + 1);
                    // console.log('#data is:' + contact.access + '\nend:' + end + '\n' + 'start' + start);
                    data.push(remote);
                    remote = { name: "", active: "" }
                    endNumber = 1;

                }
            }
            console.log('@@remote data is:' + data);

            break;


        case 'zones:\n':
            let zone = { name: '', active: '' }
            for (let i = 1; i <= (content.length - 1); i++) {
                console.log('content length' + content.length + '\nendNumber:' + endNumber);

                letter = content.slice(i - 1, i);
                console.log('letter is:' + letter)
                if (letter == ':' && endNumber == 2) {
                    end = i;

                    zone.name = content.slice(start, end - 1);

                    console.log('@@end is:' + end + '\nitem1' + zone.name)
                        // console.log('##end is:' + end + '\nnumber is:' + contact.number);
                    start = i;
                    endNumber = 3;




                }
                if (letter == ':' && endNumber == 1) {
                    start = i;
                    endNumber = 2;
                    console.log('##start is:' + start)

                }

                if (letter == ':' && endNumber == 3) {
                    start = i;
                    endNumber = 4;



                    // end = i;
                    // contact.access = content.slice(start, (i == content.length ? end : (end - 1)));
                    // endNumber = 1;
                    // console.log('##access is' + contact.access + '\t' + 'number is' + contact.number);
                    // data.push(contact);

                    // // console.log('##data is:\n' + contact.access + '\t' + contact.number);
                    // contact.number = '';
                    // contact.access = '';

                }
                if ((letter = '\n' || i == (content.length - 1)) && endNumber == 4) {
                    end = i;

                    zone.active = content.slice(start, end + 1);
                    console.log('@@')
                        // console.log('#data is:' + contact.access + '\nend:' + end + '\n' + 'start' + start);
                    data.push(zone);
                    zone = { name: "", active: "" }
                    endNumber = 1;
                    console.log('@@zone is:' + zone);

                }
            }


            break;


        case 'text:\n':
            data.push(message.slice(6));
            console.log(data[0]);
            break;
        case "out:\n":
            console.log('parse status message')
            for (let i = 1; i <= content.length; i++) {
                letter = content.slice(i - 1, i);
                if (letter == ':') {
                    start = i;


                }
                if (letter == '\n' || i == content.length) {
                    end = i;
                    data.push(content.slice(start, end));

                }
            }

            break;




        default:
            console.log('sms not ok!\n' + 'typ of message is:' + type.slice(0, type.length - 2) + '\n' + 'data is : ' + data);
            break;

    }
    console.log('typ of message is:' + type.slice(0, type.length - 1) + 'data is : ' + data);
    console.log({
        type: 'UPDATE_' + type.slice(0, type.length - 1),
        data: data,
        asset: asset
    })
    return {
        type: 'UPDATE_' + type.slice(0, type.length - 1),
        data: data,
        asset: asset
    }



}

