import React, { useState } from 'react';
import './OutPuts.css';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import Command from './Command';

const OutPuts=(props)=>{
    // const name=useSelector(state=>state.Device.name);
    const number=useSelector(state=>state.Device.number);
    const name=useSelector(state=>state.Device.name);
    const password=useSelector(state=>state.Device.password);
    const go_to_setting=()=>{
        props.changePage('setting')
    }
    const [marginLo1,setMarginLo1]=useState('1');
    const [marginLo2,setMarginLo2]=useState('1');
    const [stateTextO1,setStateTextO1]=useState('on');
    const [stateTextO2,setStateTextO2]=useState('on');
    const bgColorOn='#0afc96';
    const bgColorOff=' rgb(39, 37, 37)';

    const change_state=(event)=>{
        console.log('request for change state output')
        console.log(event.target.id)
        if(event.target.id=='o1'){
            if(marginLo1=='1'){
                setMarginLo1('0');
                setStateTextO1('off');
            }else{
                setMarginLo1('1');
                setStateTextO1('on');
            }
            
        }else{
            if(marginLo2=='1'){
                setMarginLo2('0');
                setStateTextO2('off');
            }else{
                setMarginLo2('1');
                setStateTextO2('on');
            }
            
        }

    }
    
    return(
        <div className="OutPuts">        <div  className='bg-Setting'><div  className='bgb-Setting'>
            <Command name={name} isIcon={true}></Command>
            <div className="blure"></div>
            <div className="header-Setting">
            <Link to={'/Setting'}><div className="backPart-Setting" onClick={go_to_setting}>
                    

                </div></Link>
                <div className="titlPart-Setting" >
                    <p style={{fontWeight:'400',fontSize:'medium'}}>{name}</p>

                </div>
                <a href={"sms:"+number+'?&body=*'+password+'%23OUT?'}><div className="refresh">
                    <div className="icon-refresh"></div>
                    
                    <div className="text-refresh">
                        {/* <p style={{fontWeight:'0'}}>بروز رسانی</p> */}

                    </div>
                </div></a>

            </div>
            

            <div className="part-OutPuts" id='part1-OutPuts'>
                {
                    props.data.map(item=>{
                        // 
                        return(
                            <div className="item-OutPuts" key={item.key}>
                                <div className="state-OutPuts">
                                    {/* <div className="inputPart-OutPuts" id={item.key} onClick={change_state} style={{border:item.key=='o1' ? (marginLo1=='1' ? ('1pt solid '+bgColorOn):('1pt solid '+bgColorOff)):(marginLo2=='1' ? ('1pt solid '+bgColorOn):('1pt solid'+bgColorOff))}}>
                                        <span id={item.key}>{item.key=='o1' ? stateTextO1:stateTextO2} </span>
                                        <div className="input-OutPuts" id={item.key} style={{order:(item.key=='o1' ? marginLo1 : marginLo2)}}>

                                        </div>
                                    </div> */}
                                                                        <div className="on-OutPut">
                                        <p>on</p>
                                    </div>
                                    <div className="off-OutPut">
                                        <p>off</p>
                                    </div>


                                </div>
                                <div className="name-OutPuts" >                                    
                                    <span style={{marginRight:'5px'}}><p> {item.state} </p></span>
                                    <span><p>    :{item.name}</p></span>
                                </div>
                                
                            </div>
                        )
                    })
                }

            </div>
            </div></div></div>

       
    )
}

export default OutPuts;